.axpc_window {
    width: 180px;
    height: auto;
    position: absolute;
    z-index: 1000;
    color: #fff;
    background-color: rgb(0, 0, 0, 0.3);
    box-shadow: 4px 4px 8px #000a;
    padding: 2px;
    border-radius: 8px;
    /*
    overflow: hidden;
    */
    /*  */
    -webkit-backdrop-filter: blur(3px);
    backdrop-filter: blur(3px);
    line-height: 14px;
}

/*  */
.axpc_window_attention {
    background-color: rgb(255, 255, 0, 0.3);
}

/*  */
.axpc_window_header {
    margin-top: -24px;
    justify-content: right;
    font-size: 15px;
    /* #axp_mainfont-size() */
    display: grid;
    grid-template-columns: minmax(18px, 30px) minmax(0px, 1fr) 24px;
    /* minmax(18px, 30px)imgminmax(0px, 1fr)24px */
    margin: -2px -2px 0;
    /* axpc_window */
    padding: 0;
    height: 30px;
}

/*   */
.axpc_window_header>div:first-of-type {
    font-size: 13px;
    height: 30px;
    line-height: 30px;
    overflow: hidden;
    /* grid 1/1/2/3; */
    grid-area: 1/2/2/3;
}

/*  span */
.axpc_window_header>span {
    display: inline-flex;
    align-items: center;
    /* img */
    justify-content: center;
    /* grid */
    grid-area: 1/1/2/2;
}

/*  */
.axpc_window_header_dragZone {
    cursor: move;
    height: 30px;
    grid-area: 1/1/2/3;
    /* grid */
    margin-left: -2px;
    /* safari */
    isolation: isolate;
}

/*  */
.axpc_window_header_minimizeButton {
    background-color: #0000;
    border: 0;
    cursor: pointer;
    grid-area: 1/3/2/4;
    position: relative;
    border-top-right-radius: 8px;
}

.axpc_window_header_minimizeButton:hover {
    background-color: #f009;
}

.axpc_window_header_minimizeButton::before,
.axpc_window_header_minimizeButton::after {
    background-color: #ddd;
    border-radius: 15% 15% 15% 15% / 50% 50% 50% 50%;
    content: "";
    display: block;
    height: 15%;
    left: 10%;
    position: absolute;
    top: 42%;
    width: 80%;
}

.axpc_window_header_minimizeButton::before {
    transform: rotate(135deg);
}

.axpc_window_header_minimizeButton::after {
    transform: rotate(45deg);
}

/*  */
.axpc_window_content {
    background-color: rgba(0, 0, 0, 0.5);
    padding: 5px;
    border-radius: 8px;
}

/* class */
.axpc_window_minimize,
.axpc_window_hidden {
    display: none;
}`, "",{"version":3,"sources":["webpack://./src/css/window.css"],"names":[],"mappings":"AAAA,eAAe;;AAEf;IACI,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,aAAa;IACb,WAAW;IACX,mCAAmC;IACnC,6BAA6B;IAC7B,YAAY;IACZ,kBAAkB;IAClB;;KAEC;IACD,YAAY;IACZ,kCAAkC;IAClC,0BAA0B;IAC1B,iBAAiB;AACrB;;AAEA,mBAAmB;AACnB;IACI,uCAAuC;AAC3C;;AAEA,UAAU;AACV;IACI,iBAAiB;IACjB,sBAAsB;IACtB,eAAe;IACf,gCAAgC;IAChC,aAAa;IACb,+DAA+D;IAC/D,sDAAsD;IACtD,mBAAmB;IACnB,oCAAoC;IACpC,UAAU;IACV,YAAY;AAChB;;AAEA,YAAY;AACZ;IACI,eAAe;IACf,YAAY;IACZ,iBAAiB;IACjB,gBAAgB;IAChB,gCAAgC;IAChC,kBAAkB;AACtB;;AAEA,gBAAgB;AAChB;IACI,oBAAoB;IACpB,mBAAmB;IACnB,YAAY;IACZ,uBAAuB;IACvB,aAAa;IACb,kBAAkB;AACtB;;AAEA,aAAa;AACb;IACI,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,aAAa;IACb,iBAAiB;IACjB,WAAW;IACX,kBAAkB;AACtB;;AAEA,WAAW;AACX;IACI,uBAAuB;IACvB,SAAS;IACT,eAAe;IACf,kBAAkB;IAClB,kBAAkB;IAClB,4BAA4B;AAChC;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;;IAEI,sBAAsB;IACtB,gDAAgD;IAChD,WAAW;IACX,cAAc;IACd,WAAW;IACX,SAAS;IACT,kBAAkB;IAClB,QAAQ;IACR,UAAU;AACd;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,wBAAwB;AAC5B;;AAEA,QAAQ;AACR;IACI,oCAAoC;IACpC,YAAY;IACZ,kBAAkB;AACtB;;AAEA,oBAAoB;AACpB;;IAEI,aAAa;AACjB","sourcesContent":["/*  */\n\n.axpc_window {\n    width: 180px;\n    height: auto;\n    position: absolute;\n    z-index: 1000;\n    color: #fff;\n    background-color: rgb(0, 0, 0, 0.3);\n    box-shadow: 4px 4px 8px #000a;\n    padding: 2px;\n    border-radius: 8px;\n    /*\n    overflow: hidden;\n    */\n    /*  */\n    -webkit-backdrop-filter: blur(3px);\n    backdrop-filter: blur(3px);\n    line-height: 14px;\n}\n\n/*  */\n.axpc_window_attention {\n    background-color: rgb(255, 255, 0, 0.3);\n}\n\n/*  */\n.axpc_window_header {\n    margin-top: -24px;\n    justify-content: right;\n    font-size: 15px;\n    /* #axp_mainfont-size() */\n    display: grid;\n    grid-template-columns: minmax(18px, 30px) minmax(0px, 1fr) 24px;\n    /* minmax(18px, 30px)imgminmax(0px, 1fr)24px */\n    margin: -2px -2px 0;\n    /* axpc_window */\n    padding: 0;\n    height: 30px;\n}\n\n/*