#axp_saveload {
    position: absolute;
    z-index: 5000;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.5);
}

#axp_saveload>div {
    background-color: #eee;
    color: #000;
    border-color: #337;
    border-width: 4px;
    border-style: double;
    margin: 8px;
    padding: 8px;
    border-radius: 5px;
}

/* HTML */
#axp_saveload_div_insertHTML {
    background-color: #aaa;
    border: solid 0px;
    display: flex;
    justify-content: normal;
    flex-wrap: wrap;
    padding: 8px;
    cursor: pointer;
}

/*  */
#axp_saveload_button_close {
    margin-top: 8px;
    font-weight: bolder;
    font-size: 15px;
    width: 100%;
}

.axpc_saveload_saveSlot,
.axpc_saveload_loadSlot {
    background-color: #ddd;
    width: 135px;
    min-height: 180px;
    border: solid 2px #000;
    border-radius: 10px;
    text-align: center;
    padding-top: 4px;
    font-size: 12px;
    font-weight: bold;
    line-height: 15px;
}

.axpc_saveload_saveSlot:hover {
    background-color: #faa;
}

.axpc_saveload_loadSlot:hover {
    background-color: #ffa;
}

.axpc_saveload_thumbnall {
    width: 124px;
    height: 124px;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border: #aaa solid 1px;
}

.axpc_saveload_refId {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
    word-break: break-all;
    color: #00f;
    /*  */
    max-height: 30px;
}`, "",{"version":3,"sources":["webpack://./src/css/saveload.css"],"names":[],"mappings":"AAAA,0BAA0B;;AAE1B;IACI,kBAAkB;IAClB,aAAa;IACb,OAAO;IACP,MAAM;IACN,YAAY;IACZ,WAAW;IACX,cAAc;IACd,oCAAoC;AACxC;;AAEA;IACI,sBAAsB;IACtB,WAAW;IACX,kBAAkB;IAClB,iBAAiB;IACjB,oBAAoB;IACpB,WAAW;IACX,YAAY;IACZ,kBAAkB;AACtB;;AAEA,0BAA0B;AAC1B;IACI,sBAAsB;IACtB,iBAAiB;IACjB,aAAa;IACb,uBAAuB;IACvB,eAAe;IACf,YAAY;IACZ,eAAe;AACnB;;AAEA,WAAW;AACX;IACI,eAAe;IACf,mBAAmB;IACnB,eAAe;IACf,WAAW;AACf;;AAEA;;IAEI,sBAAsB;IACtB,YAAY;IACZ,iBAAiB;IACjB,sBAAsB;IACtB,mBAAmB;IACnB,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;IACf,iBAAiB;IACjB,iBAAiB;AACrB;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,sBAAsB;AAC1B;;AAEA;IACI,gBAAgB;IAChB,oBAAoB;IACpB,qBAAqB;IACrB,aAAa;IACb,4BAA4B;IAC5B,qBAAqB;IACrB,WAAW;IACX,6BAA6B;IAC7B,gBAAgB;AACpB","sourcesContent":["/*  */\n\n#axp_saveload {\n    position: absolute;\n    z-index: 5000;\n    left: 0;\n    top: 0;\n    height: 100%;\n    width: 100%;\n    overflow: auto;\n    background-color: rgba(0, 0, 0, 0.5);\n}\n\n#axp_saveload>div {\n    background-color: #eee;\n    color: #000;\n    border-color: #337;\n    border-width: 4px;\n    border-style: double;\n    margin: 8px;\n    padding: 8px;\n    border-radius: 5px;\n}\n\n/* HTML */\n#axp_saveload_div_insertHTML {\n    background-color: #aaa;\n    border: solid 0px;\n    display: flex;\n    justify-content: normal;\n    flex-wrap: wrap;\n    padding: 8px;\n    cursor: pointer;\n}\n\n/*  */\n#axp_saveload_button_close {\n    margin-top: 8px;\n    font-weight: bolder;\n    font-size: 15px;\n    width: 100%;\n}\n\n.axpc_saveload_saveSlot,\n.axpc_saveload_loadSlot {\n    background-color: #ddd;\n    width: 135px;\n    min-height: 180px;\n    border: solid 2px #000;\n    border-radius: 10px;\n    text-align: center;\n    padding-top: 4px;\n    font-size: 12px;\n    font-weight: bold;\n    line-height: 15px;\n}\n\n.axpc_saveload_saveSlot:hover {\n    backgrou