___CSS_LOADER_EXPORT___.push([module.id, `/*  */

#axp_tool {
    width: 300px;
}

#axp_tool_div_content {
    display: flex;
}

/*  */
#axp_tool_div_leftSide {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    align-content: flex-start;
}

/*  */
#axp_tool_div_canvasSize {
    text-align: center;
}

/*  */
#axp_tool_span_canvasSize {
    font-size: 13px;
}

/*  */
#axp_tool_div_minimap_outer {
    cursor: grab;
    position: relative;
    width: 132px;
    height: 132px;
    margin-right: 4px;
    margin-top: 2px;
    margin-bottom: 4px;
    border: 6px solid #888;
}

/*  */
#axp_tool_div_minimap_inner {
    width: 120px;
    height: 120px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #777;
}

/*  */
#axp_tool_div_minimap_line {
    position: absolute;
    border: 2px #f00 solid;
}

/*  */
#axp_tool_div_rightSide {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    flex-direction: row;
    gap: 0px 6px;
    margin-top: 2px;
    margin-left: 4px;
}

/* 100% */
#axp_tool_button_loupeReset {
    width: 62px;
    margin: 0 5px 0 1px;
}

/*  */
#axp_tool_button_loupeUp,
#axp_tool_button_loupeDown {
    width: 30px;
    margin-right: 4px;
}

/*    */
#axp_tool_button_undo,
#axp_tool_button_redo,
#axp_tool_button_restore {
    width: 100%
}

/*       */
#axp_tool_button_save,
#axp_tool_button_load,
#axp_tool_button_flipH,
#axp_tool_button_flipV,
#axp_tool_button_transparent,
#axp_tool_div_gridbutton {
    width: 70px;
}

/*  */
#axp_tool_div_gridbutton {
    display: flex;
}

/*  */
#axp_tool_button_grid {
    width: 50px;
}

/*  */
#axp_tool_button_gridConfig {
    isolation: isolate;
    height: 100%;
    width: 20px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-position: 0% 50%;
    background-repeat: no-repeat;
}

/* UI */
#axp_tool_div_gridConfig {
    padding: 4px 0;
}

#axp_tool_div_gridConfig>div {
    display: flex;
    column-gap: 8px;
}

/*  */
#axp_tool_div_gridConfig>div>div:nth-of-type(2) {
    display: flex;
    padding-top: 4px;
}

/*  */
.axpc_tool_div_gridConfigButton {
    display: flex;
    border: 2px solid #555;
    border-radius: 8px;
    column-gap: 8px;
    margin-bottom: 2px;
}

/*  */
.axpc_tool_div_gridConfigButton>div:nth-of-type(1) {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #666;
    width: 50px;
}

/* px */
#axp_tool_number_gridPixel1,
#axp_tool_number_gridPixel2 {
    border-radius: 5px;
    width: 60px;
    height: 32px;
    font-family: "Arial Black", Arial, sans-serif;
    padding-left: 2px;
}

/*  */
/*  */
#axp_gridconfig_range_gridColor {
    width: 100% !important;
}`, "",{"version":3,"sources":["webpack://./src/css/window_tool.css"],"names":[],"mappings":"AAAA,UAAU;;AAEV;IACI,YAAY;AAChB;;AAEA;IACI,aAAa;AACjB;;AAEA,YAAY;AACZ;IACI,aAAa;IACb,eAAe;IACf,uBAAuB;IACvB,uBAAuB;IACvB,yBAAyB;AAC7B;;AAEA,kBAAkB;AAClB;IACI,kBAAkB;AACtB;;AAEA,eAAe;AACf;IACI,eAAe;AACnB;;AAEA,eAAe;AACf;IACI,YAAY;IACZ,kBAAkB;IAClB,YAAY;IACZ,aAAa;IACb,iBAAiB;IACjB,eAAe;IACf,kBAAkB;IAClB,sBAAsB;AAC1B;;AAEA,eAAe;AACf;IACI,YAAY;IACZ,aAAa;IACb,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,gBAAgB;AACpB;;AAEA,iCAAiC;AACjC;IACI,kBAAkB;IAClB,sBAAsB;AAC1B;;AAEA,YAAY;AACZ;IACI,aAAa;IACb,eAAe;IACf,2BAA2B;IAC3B,mBAAmB;IACnB,YAAY;IACZ,eAAe;IACf,gBAAgB;AACpB;;AAEA,SAAS;AACT;IACI,WAAW;IACX,mBAAmB;AACvB;;AAEA,aAAa;AACb;;IAEI,WAAW;IACX,iBAAiB;AACrB;;AAEA,gBAAgB;AAChB;;;IAGI;AACJ;;AAEA,iCAAiC;AACjC;;;;;;IAMI,WAAW;AACf;;AAEA,YAAY;AACZ;IACI,aAAa;AACjB;;AAEA,QAAQ;AACR;IACI,WAAW;AACf;;AAEA,kBAAkB;AAClB;IACI,kBAAkB;IAClB,YAAY;IACZ,WAAW;IACX,yDAAkD;IAClD,2BAA2B;IAC3B,4BAA4B;AAChC;;AAEA,YAAY;AACZ;IACI,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,eAAe;AACnB;;AAEA,iBAAiB;AACjB;IACI,aAAa;IACb,gBAAgB;AACpB;;AAEA,qBAAqB;AACrB;IACI,aAAa;IACb,sBAAsB;IACtB,kBAAkB;IAClB,eAAe;IACf,kBAAkB;AACtB;;AAEA,aAAa;AACb;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,gBAAgB;IAChB,WAAW;AACf;;AAEA,iBAAiB;AACjB;;IAEI,kBAAkB;IAClB,WAAW;IACX,YAAY;IACZ,6CAA6C;IAC7C,iBAAiB;AACrB;;AAEA,kBAAkB;AAClB,SAAS;AACT;IACI,sBAAsB;AAC1B","sourcesContent":["/*  */\n\n#axp_tool {\n    width: 300px;\n}\n\n#axp_tool_div_content {\n    display: flex;\n}\n\n/*  */\n#axp_tool_div_leftSide {\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: center;\n    align-items: flex-start;\n    align-content: flex-start;\n}\n\n/*  */\n#axp_tool_div_canvasSize {\n    text-align: center;\n}\n\n/*  */\n#axp_tool_span_canvasSize {\n    font-size: 13px;\n}\n\n/*  */\n#axp_tool_div_minimap_outer {\n    cursor: grab;\n    position: relative;\n    width: 132px;\n    height: 132px;\n    margin-right: 4px;\n    margin-top: 2px;\n    margin-bottom: 4px;\n    border: 6px