___CSS_LOADER_EXPORT___.push([module.id, `/*  */

.axpc_range {
    position: relative;
}

.axpc_range_label {
    pointer-events: none;
    z-index: 9999;
    position: absolute;
    top: 0;
    color: #fff;
    font-size: 12px;
    padding-left: 4px;
    padding-top: 1px;
    line-height: 14px;
}

.axpc_range_value {
    pointer-events: none;
    position: absolute;
    z-index: 9999;
    top: 0;
    width: 100px;
    color: #fff;
    font-size: 16px;
    font-family: "Arial Black", Arial, sans-serif;
    padding-left: 4px;
    padding-top: 15px;
    line-height: 14px;
}

.axpc_range input[type="range"] {
    appearance: none;
    position: relative;
    overflow: hidden;
    height: 32px;
    width: 100px;
    cursor: pointer;
    border-radius: 8px;
    border: 3px solid #444;
}

/* webkit */
/*  */
.axpc_range input[type="range"]::-webkit-slider-runnable-track {
    background: linear-gradient(#000, #555);
}

.axpc_range input[type="range"]::-webkit-slider-thumb {
    appearance: none;
    width: 32px;
    height: 32px;
    /*  */
    background: #999;
    border: 4px solid #777;
    border-radius: 8px;
    /*  */
    box-shadow: -100vw 0 0 99vw #777;
}

/* moz */
/*  */
.axpc_range input[type="range"]::-moz-range-track {
    height: 32px;
    background: linear-gradient(#000, #555);
}

.axpc_range input[type="range"]::-moz-range-thumb {
    height: 32px;
    width: 32px;
    /*  */
    background: #999;
    border: 4px solid #777;
    border-radius: 8px !important;
    /*  */
    box-shadow: -100vw 0 0 100vw #777;
    box-sizing: border-box;
}

#axp_layer_form_alpha input,
#axp_layer_form_alpha div {
    /* width */
    width: 129px !important;
}

#axp_palette_form_column input,
#axp_palette_form_column div {
    /* width */
    width: 110px !important;
}

.axpc_range_width228 input,
.axpc_range_width228 div {
    /* width */
    width: 228px !important;
}

#axp_tool_form_gridH *,
#axp_tool_form_gridV * {
    /* width */
    width: 118px !important;
}`, "",{"version":3,"sources":["webpack://./src/css/input_range.css"],"names":[],"mappings":"AAAA,eAAe;;AAEf;IACI,kBAAkB;AACtB;;AAEA;IACI,oBAAoB;IACpB,aAAa;IACb,kBAAkB;IAClB,MAAM;IACN,WAAW;IACX,eAAe;IACf,iBAAiB;IACjB,gBAAgB;IAChB,iBAAiB;AACrB;;AAEA;IACI,oBAAoB;IACpB,kBAAkB;IAClB,aAAa;IACb,MAAM;IACN,YAAY;IACZ,WAAW;IACX,eAAe;IACf,6CAA6C;IAC7C,iBAAiB;IACjB,iBAAiB;IACjB,iBAAiB;AACrB;;AAEA;IACI,gBAAgB;IAChB,kBAAkB;IAClB,gBAAgB;IAChB,YAAY;IACZ,YAAY;IACZ,eAAe;IACf,kBAAkB;IAClB,sBAAsB;AAC1B;;AAEA,WAAW;AACX,OAAO;AACP;IACI,uCAAuC;AAC3C;;AAEA;IACI,gBAAgB;IAChB,WAAW;IACX,YAAY;IACZ,QAAQ;IACR,gBAAgB;IAChB,sBAAsB;IACtB,kBAAkB;IAClB,OAAO;IACP,gCAAgC;AACpC;;AAEA,QAAQ;AACR,OAAO;AACP;IACI,YAAY;IACZ,uCAAuC;AAC3C;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,QAAQ;IACR,gBAAgB;IAChB,sBAAsB;IACtB,6BAA6B;IAC7B,OAAO;IACP,iCAAiC;IACjC,sBAAsB;AAC1B;;AAEA;;IAEI,qBAAqB;IACrB,uBAAuB;AAC3B;;AAEA;;IAEI,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;;IAEI,qBAAqB;IACrB,uBAAuB;AAC3B;;AAEA;;IAEI,qBAAqB;IACrB,uBAAuB;AAC3B","sourcesContent":["/*  */\n\n.axpc_range {\n    position: relative;\n}\n\n.axpc_range_label {\n    pointer-events: none;\n    z-index: 9999;\n    position: absolute;\n    top: 0;\n    color: #fff;\n    font-size: 12px;\n    padding-left: 4px;\n    padding-top: 1px;\n    line-height: 14px;\n}\n\n.axpc_range_value {\n    pointer-events: none;\n    position: absolute;\n    z-index: 9999;\n    top: 0;\n    width: 100px;\n    color: #fff;\n    font-size: 16px;\n    font-family: \"Arial Black\", Arial, sans-serif;\n    padding-left: 4px;\n    padding-top: 15px;\n    line-height: 14px;\n}\n\n.axpc_range input[type=\"range\"] {\n    appearance: none;\n    position: relative;\n    overflow: hidden;\n    height: 32px;\n    width: 100px;\n    cursor: pointer;\n    border-radius: 8px;\n    border: 3px solid #444;\n}\n\n/* webkit */\n/*  */\n.axpc_range input[type=\"range\"]::-webkit-slider-runnable-track {\n    background: linear-gradient(#000, #555);\n}\n\n.axpc_range input[type=\"range\"]::-webkit-slider-thumb {\n    appear