// @description 

import { ToolWindow } from './window.js';
import htmldata from '../html/window_layer.txt';
import { isBlankImage, flip_horizontal, flip_vertical, dispDate } from './etc.js';
// css
require('../css/window_layer.css');

const colorTagListDefault = [
    { name: '', color: '#f33' },
    { name: '', color: '#fa3' },
    { name: '', color: '#ff3' },
    { name: '', color: '#5f5' },
    { name: '', color: '#5ff' },
    { name: '', color: '#55f' },
    { name: '', color: '#faf' },
    { name: '', color: '#fff' },
];

