.axpc_button {
    font-family: monospace, sans-serif;
    cursor: pointer;
    height: 30px;
    padding: 0px 0px;
    text-decoration: none;
    text-align: center;
    border-top: 2px solid #777;
    border-left: 2px solid #777;
    border-right: 2px solid #333;
    border-bottom: 2px solid #333;
    border-radius: 5px;
    color: #eee;
    background: #555;
    font-size: 14px;
}

.axpc_button:hover {
    /*
    background: #888;
    */
    border: 2px solid #bbb;
}

.axpc_button:active {
    background: linear-gradient(#222, #444);
    border: 2px solid #333;
}

.axpc_button[data-selected="true"] {
    background: #aaa;
}

.axpc_button:disabled {
    background: #aaa;
}

.axpc_button_width260 {
    margin-top: 4px;
    width: 260px;
}`, "",{"version":3,"sources":["webpack://./src/css/input_button.css"],"names":[],"mappings":"AAAA,UAAU;AACV;IACI,kCAAkC;IAClC,eAAe;IACf,YAAY;IACZ,gBAAgB;IAChB,qBAAqB;IACrB,kBAAkB;IAClB,0BAA0B;IAC1B,2BAA2B;IAC3B,4BAA4B;IAC5B,6BAA6B;IAC7B,kBAAkB;IAClB,WAAW;IACX,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI;;KAEC;IACD,sBAAsB;AAC1B;;AAEA;IACI,uCAAuC;IACvC,sBAAsB;AAC1B;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,eAAe;IACf,YAAY;AAChB","sourcesContent":["/*  */\n.axpc_button {\n    font-family: monospace, sans-serif;\n    cursor: pointer;\n    height: 30px;\n    padding: 0px 0px;\n    text-decoration: none;\n    text-align: center;\n    border-top: 2px solid #777;\n    border-left: 2px solid #777;\n    border-right: 2px solid #333;\n    border-bottom: 2px solid #333;\n    border-radius: 5px;\n    color: #eee;\n    background: #555;\n    font-size: 14px;\n}\n\n.axpc_button:hover {\n    /*\n    background: #888;\n    */\n    border: 2px solid #bbb;\n}\n\n.axpc_button:active {\n    background: linear-gradient(#222, #444);\n    border: 2px solid #333;\n}\n\n.axpc_button[data-selected=\"true\"] {\n    background: #aaa;\n}\n\n.axpc_button:disabled {\n    background: #aaa;\n}\n\n.axpc_button_width260 {\n    margin-top: 4px;\n    width: 260px;\n}"],"sourceRoot":""}]);
