___CSS_LOADER_EXPORT___.push([module.id, `#axp_launcher {
    width: auto;
}

/*  */
#axp_launcher_div_buttons {
    display: flex;
}

/*  */
#axp_launcher_div_personalButtonsEntry {
    display: flex;
}

/*  */
.axpc_launcher_personalButton,
.axpc_launcher_allButton {
    position: relative;
    cursor: pointer;
    border: 2px solid #000;
    border-radius: 5px;
    width: 40px;
    height: 40px;
    margin: 0 2px;
    background-color: #ddd;
    border: 2px solid #aaa;
    padding: 0;
}

/*  */
.axpc_launcher_personalButton>div,
.axpc_launcher_allButton>div {
    pointer-events: none;
    width: 28px;
    height: 28px;
    margin: auto auto;
    opacity: 1;
}

/*  */
.axpc_launcher_personalButton.axpc_launcher_minimize {
    background-color: #888;
}

.axpc_launcher_personalButton.axpc_launcher_minimize>div {
    opacity: 0.3;
}

/*  */
.axpc_launcher_personalButton:hover,
.axpc_launcher_allButton:hover {
    border: 2px solid #eee;
    background-color: #bbb;
}

/*  */
.axpc_launcher_personalButton:active,
.axpc_launcher_allButton:active {
    border: 2px solid #555;
    background-color: #555;
}

/*  */
.axpc_launcher_sizeSmall {
    width: 32px;
    height: 32px;
}

.axpc_launcher_sizeBig {
    width: 48px;
    height: 48px;
}

#axp_launcher_separator {
    width: 2px;
    height: auto;
    background-color: #888;
    margin: 0 2px;
}`, "",{"version":3,"sources":["webpack://./src/css/window_launcher.css"],"names":[],"mappings":"AAAA;IACI,WAAW;AACf;;AAEA,eAAe;AACf;IACI,aAAa;AACjB;;AAEA,YAAY;AACZ;IACI,aAAa;AACjB;;AAEA,aAAa;AACb;;IAEI,kBAAkB;IAClB,eAAe;IACf,sBAAsB;IACtB,kBAAkB;IAClB,WAAW;IACX,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,sBAAsB;IACtB,UAAU;AACd;;AAEA,mBAAmB;AACnB;;IAEI,oBAAoB;IACpB,WAAW;IACX,YAAY;IACZ,iBAAiB;IACjB,UAAU;AACd;;AAEA,gBAAgB;AAChB;IACI,sBAAsB;AAC1B;;AAEA;IACI,YAAY;AAChB;;AAEA,gBAAgB;AAChB;;IAEI,sBAAsB;IACtB,sBAAsB;AAC1B;;AAEA,cAAc;AACd;;IAEI,sBAAsB;IACtB,sBAAsB;AAC1B;;AAEA,WAAW;AACX;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,UAAU;IACV,YAAY;IACZ,sBAAsB;IACtB,aAAa;AACjB","sourcesContent":["#axp_launcher {\n    width: auto;\n}\n\n/*  */\n#axp_launcher_div_buttons {\n    display: flex;\n}\n\n/*  */\n#axp_launcher_div_personalButtonsEntry {\n    display: flex;\n}\n\n/*  */\n.axpc_launcher_personalButton,\n.axpc_launcher_allButton {\n    position: relative;\n    cursor: pointer;\n    border: 2px solid #000;\n    border-radius: 5px;\n    width: 40px;\n    height: 40px;\n    margin: 0 2px;\n    background-color: #ddd;\n    border: 2px solid #aaa;\n    padding: 0;\n}\n\n/*  */\n.axpc_launcher_personalButton>div,\n.axpc_launcher_allButton>div {\n    pointer-events: none;\n    width: 28px;\n    height: 28px;\n    margin: auto auto;\n    opacity: 1;\n}\n\n/*  */\n.axpc_launcher_personalButton.axpc_launcher_minimize {\n    background-color: #888;\n}\n\n.axpc_launcher_personalButton.axpc_launcher_minimize>div {\n    opacity: 0.3;\n}\n\n/*  */\n.axpc_launcher_personalButton:hover,\n.axpc_launcher_allButton:hover {\n    border: 2px solid #eee;\n    background-color: #bbb;\n}\n\n/*  */\n.axpc_launcher_personalButton:active,\n.axpc_launcher_allButton:active {\n    border: 2px solid #555;\n    background-color: #555;\n}\n\n/*  */\n.axpc_launcher_sizeSmall {\n    width: 32px;\n    height: 32px;\n}\n\n.axpc_launcher_sizeBig {\n    width: 48px;\n    height: 48px;\n}\n\n#axp_laun