// @description 

import { Round } from './round.js';

// 
export class Square extends Round {
    constructor(option) {
        super(option);
        // Round
        this.name = this.axpObj._('@PENNAME.SQUARE');
        this.size = 8;
        // 
        // 
        this.borderRadius = 0;
        this.borderStyle = 'normal';
        this.lineCap = 'square';
        this.lineJoin = 'bevel';

        this.init_save();
    }
}