// @description 

import { Dot } from './dot.js';

// 
export class Crayon extends Dot {
    constructor(option) {
        super(option);
        // Round
        this.name = this.axpObj._('@PENNAME.CRAYON');
        this.size = 15;
        this.toneLevel = null;
        this.radius = 50;
        // 
        // 
        this.borderRadius = 50;
        this.borderStyle = 'normal';
        this.lineCap = 'square';
        this.lineJoin = 'bevel';

        this.init_save();
    }
    // Dot_penObj
    getCursorPosition(e) {
        const pos = this.getCursorPositionNormal(e);
        return { x: pos.x, y: pos.y };
    }
    // 
    trans_dot(point) {
        return point;
    }
    // 
    getRandomPosition(size) {
        let x, y;
        // 
        let width0 = 1 - this.radius / 50;
        // 
        let width1 = 1 - width0;

        while (true) {
            x = Math.random() * 2 - 1;
            y = Math.random() * 2 - 1;

            const absX = Math.abs(x);
            const absY = Math.abs(y);
            // 
            if (absX > width0 && absY > width0) {
                // 
            } else {
                break;
            }

            // 
            let dx = (absX - width0) / width1;
            let dy = (absY - width0) / width1;
            const root = (dx * dx) + (dy * dy);
            // 1
            if (root < 1) {
                break;
            }
        }

        x = parseInt(x * size / 2);
        y = parseInt(y * size / 2);

        return { x: x, y: y };
    }
    // 
    drawLine(
        imageData,
        x0,
        y0,
        x1,
        y1,
        lineWidth,
        color,
    ) {
        const { width, data } = imageData;
        const dx = Math.abs(x1 - x0);
        const dy = Math.abs(y1 - y0);
        const sx = (x0 < x1) ? 1 : -1;
        const sy = (y0 < y1) ? 1 : -1;
        const TIMES = 20 + lineWidth * 2;

        let err = dx - dy;
        let vx = x0;
        let vy = y0;
        let old_divx0 = -1;
        let old_divy0 = -1;

        while (true) {
            // setpixel
            let divx0 = vx;
            let divy0 = vy;
            // lineWidth
            if (divx0 !== old_divx0 || divy0 !== old_divy0) {

                for (let i = 0; i < TIMES; i++) {
                    // 
                    let pos = this.getRandomPosition(lineWidth);
                    let x = pos.x + divx0;
                    let y = pos.y + divy0;

                    let a = parseInt(Math.random() * 30) + 20;
                    // 
                    if (x >= 0 && x < this.axpObj.x_size && y >= 0 && y < this.axpObj.y_size) {
                        //console.log(x,y);
                        const i = (x + y * width) * 4
                        data[i + 0] = color[0]
                        data[i + 1] = color[1]
                        data[i + 2] = color[2]
                        data[i + 3] = a;
                    }
                }
                // 
                old_divx0 = divx0;
                old_divy0 = divy0;
            }
            // 
            if (vx === x1 && vy === y1) break;

            // 
            let e2 = 2 * err;
            if (e2 > -dy) {
                err = err - dy;
                vx = vx + sx;
            }
            if (e2 < dx) {
                err = err + dx;
                vy = vy + sy;
            }
        }
    }
    drawCircle(
        imageData,
        x0,
        y0,
        x1,
        y1,
        lineWidth,
        color,
    ) {
        const { width, data } = imageData;
        const TIMES = 20 + lineWidth * 2;
        const diff = parseInt(lineWidth / 2);
        const r = parseInt(Math.sqrt(Math.pow(x1 - x0, 2) + Math.pow(y1 - y0, 2)));
        if (r <= 0) { return };

        var xx = parseInt(128 * r);
        var yy = 0;
        var x = 0;
        var y = 0;

        const SetPixel = (divx0, divy0) => {
            let divx1 = divx0 + lineWidth;
            let divy1 = divy0 + lineWidth;

            for (let i = 0; i < TIMES; i++) {
                // 
                let x = parseInt(Math.random() * (divx1 - divx0) + divx0) - diff;
                let y = parseInt(Math.random() * (divy1 - divy0) + divy0) - diff;
                let a = parseInt(Math.random() * 30) + 20;
                // 
                if (x >= 0 && x < this.axpObj.x_size && y >= 0 && y < this.axpObj.y_size) {
                    //console.log(x,y);
                    const i = (x + y * width) * 4
                    data[i + 0] = color[0]
                    data[i + 1] = color[1]
                    data[i + 2] = color[2]
                    data[i + 3] = a;
                }
            }
        }
        while (yy <= xx) {
            x = parseInt(xx / 128);
            y = parseInt(yy / 128);
            SetPixel(x0 + x, y0 + y);
            SetPixel(x0 - x, y0 - y);
            SetPixel(x0 - x, y0 + y);
            SetPixel(x0 + x, y0 - y);
            SetPixel(x0 + y, y0 + x);
            SetPixel(x0 - y, y0 - x);
            SetPixel(x0 - y, y0 + x);
            SetPixel(x0 + y, y0 - x);
            yy += parseInt(xx / 128);
            xx -= parseInt(yy / 128);
        }

    }
}
