#axp_alert_div_modalContainer {
    background-color: rgba(0, 0, 0, 0.3);
    width: 100%;
    height: 100%;
    z-index: 10000;
    display: flex;
    position: fixed;
    align-items: center;
    justify-content: center;
}

#axp_alert_div_alertBox {
    width: 50%;
    min-width: 350px;
    max-width: 600px;
    min-height: 100px;
    max-height: 400px;
    border: 1px solid #fff;
    background-color: #fff;
    background-repeat: no-repeat;
}

#axp_alert_div_modalContainer>#alertBox {
    position: fixed;
}

#axp_alert_div_alertBox h1 {
    margin: 0;
    font: bold 1em Raleway, arial;
    background-color: #555;
    color: #FFF;
    border-bottom: 1px solid #555;
    padding: 5px 0 5px 5px;
}

#axp_alert_div_alertBox p {
    height: auto;
    padding-left: 5px;
    padding-top: 30px;
    text-align: center;
    vertical-align: middle;
    color: #000;
}

#axp_alert_div_alertBox #axp_alert_button_closeBtn {
    margin: 10px auto 10px auto;
    padding: 7px;
    border: 0 none;
    width: 100px;
    text-transform: uppercase;
    text-align: center;
    color: #FFF;
    background-color: #555;
    border-radius: 0px;
    text-decoration: none;
    outline: 0 !important;
}

#axp_alert_div_alertBox #axp_alert_button_cancelBtn {
    margin: 10px auto 10px auto;
    padding: 7px;
    border: 0 none;
    width: 100px;
    text-transform: uppercase;
    text-align: center;
    color: #FFF;
    background-color: #555;
    border-radius: 0px;
    text-decoration: none;
    outline: 0 !important;
}
