___CSS_LOADER_EXPORT___.push([module.id, `/*  */

.axpc_radio {
    font-size: 12px;
}

.axpc_radio input {
    display: none;
}

.axpc_radio input[type="radio"]:checked+label {
    font-weight: bold;
    color: #000;
    border: 2px solid #bf9701;
    background-color: #f7a312;
}

.axpc_radio label {
    color: #777;
    border: 2px solid #ddd;
    text-align: center;
    display: inline-block;
    padding: 5px 0px;
    width: 100px;
    border-radius: 0px;
}

.axpc_radio label:hover {
    color: #aaa;
    border: 2px solid #aaa;
    cursor: pointer;
}

.axpc_radio button {
    color: #777;
    border: 2px solid #000;
    text-align: center;
    display: inline-block;
    padding: 5px 0px;
    width: 100px;
    margin: 2px;
    border-radius: 50px;
}

.axpc_radio button:hover {
    color: #aaa;
    border: 2px solid #aaa;
    cursor: pointer;
}

.axpc_radio button:active {
    color: #000;
    border: 2px solid #444;
}

/* spanradiojs */
.axpc_radio span {
    display: none;
}`, "",{"version":3,"sources":["webpack://./src/css/input_radio.css"],"names":[],"mappings":"AAAA,kBAAkB;;AAElB;IACI,eAAe;AACnB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,iBAAiB;IACjB,WAAW;IACX,yBAAyB;IACzB,yBAAyB;AAC7B;;AAEA;IACI,WAAW;IACX,sBAAsB;IACtB,kBAAkB;IAClB,qBAAqB;IACrB,gBAAgB;IAChB,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,WAAW;IACX,sBAAsB;IACtB,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,sBAAsB;IACtB,kBAAkB;IAClB,qBAAqB;IACrB,gBAAgB;IAChB,YAAY;IACZ,WAAW;IACX,mBAAmB;AACvB;;AAEA;IACI,WAAW;IACX,sBAAsB;IACtB,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,sBAAsB;AAC1B;;AAEA,uCAAuC;AACvC;IACI,aAAa;AACjB","sourcesContent":["/*  */\n\n.axpc_radio {\n    font-size: 12px;\n}\n\n.axpc_radio input {\n    display: none;\n}\n\n.axpc_radio input[type=\"radio\"]:checked+label {\n    font-weight: bold;\n    color: #000;\n    border: 2px solid #bf9701;\n    background-color: #f7a312;\n}\n\n.axpc_radio label {\n    color: #777;\n    border: 2px solid #ddd;\n    text-align: center;\n    display: inline-block;\n    padding: 5px 0px;\n    width: 100px;\n    border-radius: 0px;\n}\n\n.axpc_radio label:hover {\n    color: #aaa;\n    border: 2px solid #aaa;\n    cursor: pointer;\n}\n\n.axpc_radio button {\n    color: #777;\n    border: 2px solid #000;\n    text-align: center;\n    display: inline-block;\n    padding: 5px 0px;\n    width: 100px;\n    margin: 2px;\n    border-radius: 50px;\n}\n\n.axpc_radio button:hover {\n    color: #aaa;\n    border: 2px solid #aaa;\n    cursor: pointer;\n}\n\n.axpc_radio button:active {\n    color: #000;\n    border: 2px solid #444;\n}\n\n/* spanradiojs */\n.axpc_radio span {\n    display: none;\n}"],"sourceRoot":""}]);
