___CSS_LOADER_EXPORT___.push([module.id, `/*  */
#axp_config_div_content {
    display: flex;
    margin: 0;
    padding: 0;
    height: 100%;
}

/*  */
#axp_config_div_nav {
    width: 200px;
    height: 100%;
    overflow: auto;
    position: sticky;
    top: 0;
    background-color: #eee;
    padding: 10px;
    /* iPad */
    overscroll-behavior: none;
}

/*  */
#axp_config_div_nav button {
    width: 160px;
    height: 40px;
    border-radius: 6px;
    border: 0;
    background-color: #eee;
    color: #111;
    cursor: pointer;
    margin: 1px 0;
    padding-left: 8px;
    font-size: 1em;
    font-weight: bold;
    text-align: left;
}

#axp_config_div_nav button.axpc_ACTIVE {
    background: #E09C35;
}

/*  */
.axpc_config_chapter {
    width: calc(100% - 200px);
    height: 100%;
    padding: 10px;
    background-color: #eee;
    overflow-y: scroll;
}

/* 599px */
@media (max-width: 599px) {
    #axp_config_div_nav {
        /*  */
        display: none;
    }

    .axpc_config_chapter {
        width: 100%;
    }
}

/*  */
.axpc_config_section {
    margin-bottom: 50px;
    color: #111;
}

/*  */
.axpc_config_sectionHeader {
    background: #ddf;
    margin-top: 8px;
    padding: 8px 8px;
    font-weight: bolder;
}

/* axpc_config_div_headerdiv */
.axpc_config_group {
    padding: 16px;
    border: 2px #ddd solid;
}

/*  */
.axpc_config_group a {
    overflow-wrap: anywhere;
}

/*  */
.axpc_config_groupHeader {
    background: #ddd;
    margin-top: 8px;
    padding: 4px 16px;
}

/*  */
#axp_config_ul_canvasSizeHistory {
    margin-top: 8px;
    width: 200px;
    margin: 0 0 8px 0;
    padding: 0;
    list-style-type: none;
    border: 1px solid #aaa;
    max-height: 220px;
    overflow-y: auto;
}

#axp_config_ul_canvasSizeHistory>li {
    padding: 2px 10px;
    height: 25px;
    text-align: center;
    cursor: pointer;
}

#axp_config_ul_canvasSizeHistory>li:nth-child(even) {
    background: #ddd;
}

/* even */
#axp_config_ul_canvasSizeHistory>li[data-selected="true"] {
    background: #f7a312 !important;
}

/*  */
.axpc_config_table {
    border: 2px solid #aaa;
    border-collapse: collapse;
}

.axpc_config_table td:nth-child(1) {
    font-family: 'Consolas', monospace;
    font-size: 12pt;
}

.axpc_config_table th {
    padding: 0 8px;
    background-color: #ddf;
}

.axpc_config_table td {
    padding: 0 8px;
    border: 1px solid #aaa;
}

/* */
#axp_config_div_paletteScroll {
    max-width: 228px;
    max-height: 228px;
    overflow: auto;
    background: #ccc;
}

/*  */
#axp_config_div_paletteBox {
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
    background: #333;
    padding: 4px;
}

/*  */
.axpc_config_colorRect {
    height: 20px;
    width: 20px;
    margin: 1px;
    border-radius: 3px;
}

/*  */
#axp_config_div_scale {
    width: 200px;
}

#axp_config_div_scaleButton {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 30px 1fr;
}

#axp_config_button_addScale {
    width: 100%;
}

#axp_config_button_deleteScale,
#axp_config_button_resetScale {
    margin-top: 8px;
    width: 100%;
}

/*  */
#axp_config_ul_scale {
    margin-top: 8px;
    width: 200px;
    margin: 0;
    padding: 0;
    list-style-type: none;
    border: 1px solid #aaa;
    max-height: 352px;
    overflow-y: scroll;
}

#axp_config_ul_scale>li {
    padding: 2px 10px;
    height: 25px;
    text-align: center;
    cursor: pointer;
}

#axp_config_ul_scale>li:nth-child(even) {
    background: #ddd;
}

#axp_config_ul_scale>li[data-value="100"] {
    border: 1px solid #333;
}

/* even */
#axp_config_ul_scale>li[data-selected="true"] {
    background: #f7a312 !important;
}

/*  */
#axp_config_div_colorTag {
    width: 300px;
    display: grid;
    grid-template-rows: repeat(4, 34px);
    grid-template-columns: 1fr 1fr;
    margin-bottom: 8px;
    gap: 4px 10px;
}

.axpc_config_div_colorTag {
    border-radius: 5px;
    border: #222 solid 2px;
    width: 154px;
}

.axpc_config_text_colorTag {
    width: 130px;
    height: 30px;
    font-size: 13px;
    text-align: left;
    border-radius: 5px;
    padding-left: 4px;
}

/*  */
.axpc_config_select_scaleTable {
    width: 24px;
}

/*  */
.axpc_config_number_sizeValue,
.axpc_config_number_scaleValue {
    width: 60px;
}

/*  */
#axp_config_div_versionInfo {
    -webkit-user-select: all;
    user-select: all;
}`, "",{"version":3,"sources":["webpack://./src/css/config.css"],"names":[],"mappings":"AAAA,SAAS;AACT;IACI,aAAa;IACb,SAAS;IACT,UAAU;IACV,YAAY;AAChB;;AAEA,mBAAmB;AACnB;IACI,YAAY;IACZ,YAAY;IACZ,cAAc;IACd,gBAAgB;IAChB,MAAM;IACN,sBAAsB;IACtB,aAAa;IACb,eAAe;IACf,yBAAyB;AAC7B;;AAEA,UAAU;AACV;IACI,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,SAAS;IACT,sBAAsB;IACtB,WAAW;IACX,eAAe;IACf,aAAa;IACb,iBAAiB;IACjB,cAAc;IACd,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,mBAAmB;AACvB;;AAEA,UAAU;AACV;IACI,yBAAyB;IACzB,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,kBAAkB;AACtB;;AAEA,uBAAuB;AACvB;IACI;QACI,mBAAmB;QACnB,aAAa;IACjB;;IAEA;QACI,WAAW;IACf;AACJ;;AAEA,aAAa;AACb;IACI,mBAAmB;IACnB,WAAW;AACf;;AAEA,gBAAgB;AAChB;IACI,gBAAgB;IAChB,eAAe;IACf,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA,kEAAkE;AAClE;IACI,aAAa;IACb,sBAAsB;AAC1B;;AAEA,mBAAmB;AACnB;IACI,uBAAuB;AAC3B;;AAEA,aAAa;AACb;IACI,gBAAgB;IAChB,eAAe;IACf,iBAAiB;AACrB;;AAEA,gBAAgB;AAChB;IACI,eAAe;IACf,YAAY;IACZ,iBAAiB;IACjB,UAAU;IACV,qBAAqB;IACrB,sBAAsB;IACtB,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,iBAAiB;IACjB,YAAY;IACZ,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,gBAAgB;AACpB;;AAEA,kBAAkB;AAClB;IACI,8BAA8B;AAClC;;AAEA,gBAAgB;AAChB;IACI,sBAAsB;IACtB,yBAAyB;AAC7B;;AAEA;IACI,kCAAkC;IAClC,eAAe;AACnB;;AAEA;IACI,cAAc;IACd,sBAAsB;AAC1B;;AAEA;IACI,cAAc;IACd,sBAAsB;AAC1B;;AAEA,yBAAyB;AACzB;IACI,gBAAgB;IAChB,iBAAiB;IACjB,cAAc;IACd,gBAAgB;AACpB;;AAEA,gBAAgB;AAChB;IACI,aAAa;IACb,eAAe;IACf,yBAAyB;IACzB,gBAAgB;IAChB,YAAY;AAChB;;AAEA,iBAAiB;AACjB;IACI,YAAY;IACZ,WAAW;IACX,WAAW;IACX,kBAAkB;AACtB;;AAEA,QAAQ;AACR;IACI,YAAY;AAChB;;AAEA;IACI,WAAW;IACX,aAAa;IACb,mCAAmC;AACvC;;AAEA;IACI,WAAW;AACf;;AAEA;;IAEI,eAAe;IACf,WAAW;AACf;;AAEA,YAAY;AACZ;IACI,eAAe;IACf,YAAY;IACZ,SAAS;IACT,UAAU;IACV,qBAAqB;IACrB,sBAAsB;IACtB,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,iBAAiB;IACjB,YAAY;IACZ,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,sBAAsB;AAC1B;;AAEA,kBAAkB;AAClB;IACI,8BAA8B;AAClC;;AAEA,UAAU;AACV;IACI,YAAY;IACZ,aAAa;IACb,mCAAmC;IACnC,8BAA8B;IAC9B,kBAAkB;IAClB,aAAa;AACjB;;AAEA;IACI,kBAAkB;IAClB,sBAAsB;IACtB,YAAY;A