.axpc_toggle_switch {
    position: relative;
    width: 55px;
    height: 32px;
    margin: 0px 0px;
    border-radius: 50px;
    overflow: hidden;
    cursor: pointer;
    background: #aaa5;
}

.axpc_toggle_switch input[type=checkbox] {
    display: none;
}

.axpc_toggle_switch::before {
    content: "";
    position: absolute;
    top: 0;
    left: -55px;
    width: 100%;
    height: 100%;
    display: block;
    -webkit-transition: 0.2s ease-out;
    transition: 0.2s ease-out;
    background: rgb(224, 156, 53);
}

.axpc_toggle_switch::after {
    content: "";
    position: absolute;
    top: 2px;
    left: 2px;
    width: 28px;
    height: 28px;
    display: block;
    border-radius: 50px;
    background: #fff;
    box-shadow: 0 9px 28px -6px rgba(0, 0, 0, 0.3);
    -webkit-transition: 0.2s ease-out;
    transition: 0.2s ease-out;
    text-align: center;
    padding: 9px 0 0;
    line-height: 1;
    font-size: 12px;
    font-weight: bold;
    color: #333;
    letter-spacing: .5px;
    box-sizing: border-box;
}

.axpc_toggle_switch[data-checked="true"]::before {
    left: 0;
}

.axpc_toggle_switch[data-checked="true"]::after {
    content: "";
    left: 24px;
    box-shadow: 0 9px 28px -6px rgba(0, 0, 0, 0.5);
    color: #333;
    padding: 9px 0 0 1px;
}`, "",{"version":3,"sources":["webpack://./src/css/input_toggle.css"],"names":[],"mappings":"AAAA,iBAAiB;;AAEjB;IACI,kBAAkB;IAClB,WAAW;IACX,YAAY;IACZ,eAAe;IACf,mBAAmB;IACnB,gBAAgB;IAChB,eAAe;IACf,iBAAiB;AACrB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,WAAW;IACX,kBAAkB;IAClB,MAAM;IACN,WAAW;IACX,WAAW;IACX,YAAY;IACZ,cAAc;IACd,iCAAiC;IACjC,yBAAyB;IACzB,6BAA6B;AACjC;;AAEA;IACI,YAAY;IACZ,kBAAkB;IAClB,QAAQ;IACR,SAAS;IACT,WAAW;IACX,YAAY;IACZ,cAAc;IACd,mBAAmB;IACnB,gBAAgB;IAChB,8CAA8C;IAC9C,iCAAiC;IACjC,yBAAyB;IACzB,kBAAkB;IAClB,gBAAgB;IAChB,cAAc;IACd,eAAe;IACf,iBAAiB;IACjB,WAAW;IACX,oBAAoB;IACpB,sBAAsB;AAC1B;;AAEA;IACI,OAAO;AACX;;AAEA;IACI,YAAY;IACZ,UAAU;IACV,8CAA8C;IAC9C,WAAW;IACX,oBAAoB;AACxB","sourcesContent":["/*  */\n\n.axpc_toggle_switch {\n    position: relative;\n    width: 55px;\n    height: 32px;\n    margin: 0px 0px;\n    border-radius: 50px;\n    overflow: hidden;\n    cursor: pointer;\n    background: #aaa5;\n}\n\n.axpc_toggle_switch input[type=checkbox] {\n    display: none;\n}\n\n.axpc_toggle_switch::before {\n    content: \"\";\n    position: absolute;\n    top: 0;\n    left: -55px;\n    width: 100%;\n    height: 100%;\n    display: block;\n    -webkit-transition: 0.2s ease-out;\n    transition: 0.2s ease-out;\n    background: rgb(224, 156, 53);\n}\n\n.axpc_toggle_switch::after {\n    content: \"\";\n    position: absolute;\n    top: 2px;\n    left: 2px;\n    width: 28px;\n    height: 28px;\n    display: block;\n    border-radius: 50px;\n    background: #fff;\n    box-shadow: 0 9px 28px -6px rgba(0, 0, 0, 0.3);\n    -webkit-transition: 0.2s ease-out;\n    transition: 0.2s ease-out;\n    text-align: center;\n    padding: 9px 0 0;\n    line-height: 1;\n    font-size: 12px;\n    font-weight: bold;\n    color: #333;\n    letter-spacing: .5px;\n    box-sizing: border-box;\n}\n\n.axpc_toggle_switch[data-checked=\"true\"]::before {\n    left: 0;\n}\n\n.axpc_toggle_switch[data-checked=\"true\"]::after {\n    content: \"\";\n    left: 24px;\n    box-shadow: 0 9px 28px -6px rg