body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
  /* iPad */
  height: 100svh;
  display: flex;
  flex-direction: column;
  /* Pull-to-Refresh */
  overscroll-behavior-y: none;
  /* HTML5 */
  overflow: hidden;
}

#axp_main {
  touch-action: none;
  -webkit-user-select: none;
  user-select: none;
  color: #fff;
  background-color: #000;
  font-size: 15px;
  width: 100%;
  margin: 0 auto;
  padding: 0;
  display: none;
  flex-grow: 1;
  flex-direction: column;
  overflow: hidden;
  font-family: sans-serif;
}

/* 0.0.0 */
:where(#axp_main) * {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/*  */
#axp_main_div_tab {
  display: flex;
  align-items: flex-end;
  min-height: 30px;
}

/*  */
#axp_main_div_tab_menu {
  list-style-type: none;
  display: flex;
}

/*  */
#axp_main_div_tab_menu>div {
  display: block;
  width: 98px;
  margin: 0;
  padding: 2px 0;
  text-align: center;
  border-top: 2px solid #000;
  border-left: 0px solid #000;
  border-right: 2px solid #000;
  border-radius: 10px 10px 0 0;
  background: #333;
  box-sizing: border-box;
  cursor: pointer;
}

/*  */
#axp_main_div_tab_menu>div:hover {
  background: #555;
}

/*  */
#axp_main_div_tab_menu>div[data-selected="true"] {
  background: #777;
}

/*  */
#axp_main_label_hamburger {
  /*  */
  display: none;
  height: 100%;
  width: 30px;
  cursor: pointer;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 8px;
}

/*  */
#axp_main_checkbox_hamburger {
  display: none;
}

/* 599px */
@media (max-width: 599px) {

  /*  */
  #axp_main_label_hamburger {
    display: flex;
  }

  #axp_main_div_tab_menu {
    position: absolute;
    background: #333;
    top: 0;
    margin-top: 30px;
    left: 0;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    z-index: 6000;
  }

  #axp_main_checkbox_hamburger~div>div {
    height: 0;
    margin: 0;
    padding: 0;
    border: 0;
    transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
  }

  #axp_main_checkbox_hamburger:checked~div>div {
    border: 1px solid #333;
    height: 2.5em;
    padding: 0.5em;
    transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
  }

  #axp_main_div_tab_menu>div {
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0.5em 0;
    width: 100%;
    border-radius: 10px 10px 10px 10px;
  }
}

/*  */
#axp_main_div_headerText {
  padding: 4px 10px 0;
  color: #fff;
  overflow: hidden;
  white-space: pre;
  flex-grow: 1;
  cursor: pointer;
  text-overflow: ellipsis;
}

/*  */
#axp_main_div_headerText:active {
  overflow: visible;
  white-space: normal;
}

/*  */
#axp_main_div_tabContent>article {
  display: none;
  flex-grow: 1;
  flex-direction: column;
  overflow: hidden;
}

/* input */
#axp_main_div_tabContent input {
  font-size: 13px;
}

/*  */
#axp_canvas {
  width: 100%;
  overflow: hidden;
  position: relative;
  flex-grow: 1;
}

/*  */
#axp_canvas_div_grayBackground {
  width: 100%;
  height: 100%;
  background-color: #777;
}

/*  */
#axp_canvas_canvas_main {
  position: absolute;
  image-rendering: pixelated;
  z-index: 100;
}

/*  */
#axp_canvas_div_grid {
  /*  */
  pointer-events: none;
  image-rendering: auto;
  position: absolute;
  z-index: 600;
}

/*  */
#axp_canvas_div_pointerPosition {
  position: absolute;
  right: 0;
  bottom: 0;
  font-family: monospace;
  font-size: 18px;
  font-weight: 500;
  color: #fff;
  background: rgba(0, 0, 0, 0.7);
  box-shadow: #000;
  z-index: 800;
}

/*  */
#axp_canvas_div_debugInfo {
  position: absolute;
  top: 0;
  left: 0;
  font-family: monospace;
  font-size: 1em;
  background: rgba(0, 0, 0, 0.7);
  box-shadow: #000;
  z-index: 900;
  height: 50%;
  max-height: 95%;
  resize: both;
  overflow: hidden;
}

#axp_canvas_div_debugInfo>div:nth-of-type(1) {
  font-family: monospace;
  color: #fff;
  font-size: 1em;
  height: 24px;
  overflow: hidden;
}

#axp_canvas_div_debugInfo>div:nth-of-type(2) {
  font-family: monospace;
  font-size: 1em;
  color: #fff;
  background: rgba(0, 0, 0, 0);
  min-width: 360px;
  height: calc(100% - 24px);
  overflow-x: hidden;
  overflow-y: scroll;
}

/* */
#axp_canvas_div_penCursor {
  pointer-events: none;
  position: absolute;
  border: solid 2px rgba(0, 0, 0, 0.3);
  z-index: 700;
}


#axp_main_div_tabContent,
#axp_config,
#axp_config_div_menu {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  overflow: hidden;
}

/*  */
#axp_config_div_content {
  flex-grow: 1;
}

/*  */
#axp_post {
  flex-grow: 1;
  overflow: auto;
}

/*  */
#axp_footer {
  touch-action: none;
  -webkit-user-select: none;
  user-select: none;
  background-color: #222;
  color: #ddd;
  display: flex;
  flex-basis: 31px;
}

#axp_footer_div_message {
  background-color: rgba(0, 0, 0, 0);
  font-size: 15px;
  font-family: sans-serif;
  padding: 4px;
}

#axp_footer_div_icon {
  margin-left: 8px;
  margin-top: 2px;
